.cryptocard{
    width: 300px;
    height: 220px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 5px;
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform: scale(0.9);
    margin: 5px;
    background-color: white;
}

.cryptocard h5{
    font-size: 16px;
}

.cryptocard:hover{
    transform: scale(1);
    cursor: pointer;
}

.cryptocard *{
    padding: 3px;
}