.Coinpage{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    flex-wrap: wrap;
}

.Coininfo{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    padding: 20px;
    width: 320px;
}

.Coininfo *{
    padding: 3px;
}

.Coininfo img{
    height: 150px;
    width: 150px;
}

.Coininfo div{
    text-align: left;
}

.Chart{
    width:900px;
}

@media (max-width: 900px) {
    .Chart{
        width: 330px;
    }
}

.Chart div{
    text-align: center;
}

.Chart div button{
    margin: 10px;
    background-color: rgb(64, 108, 228);
    color: white;
}